.Stories{
    display: -webkit-flex;
    display: flex;
    margin-top: 70px;
    background: var(--white);
    border:1px solid #efefef;
    border-radius: 5px;
    overflow:auto;
}
.Stories__info{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    margin:0 0.5rem;
    cursor:pointer;
}

.Stories__img{
    width:80px;
    height: 85px;
    border-radius:50% ;
    overflow: hidden;
    border:2px solid #f83a63;
    padding: 1px; 
}   
.Stories__name{
    font-size: 0.7rem;
    margin-top: 4px;
}
.Stories__img span{
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
}

.Stories__img span img{
    object-fit: cover;
    width: 100%;
    height: 100%;
} 

.Posts__input--1{
    width: 100%;
    height: 2.5rem;
    padding:0 13px;
    outline:none;
    border:1px solid #efefef;
    border-radius:2px;
}
.Posts__upload{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-top:0.7rem;
}
.Posts__icon{
    font-size:2.2rem;
    cursor: pointer;
    color: #f83a63;
}
.Post__upload--file{
    display: none;
}
.Posts__input--2{
    -webkit-flex:1 1;
            flex:1 1;
    text-align: left;
}
.Posts__submit{
    -webkit-flex: 1 1;
            flex: 1 1;
    text-align: right;
}

.btn-submit{
    background-color: #f83a63;
    color: #fff;
    border: none;
    text-decoration: none;
    padding: 5px 9px;
    border-radius: 3px;
    outline: none;
    font-size: 1.1rem;
    cursor: pointer;
}

.Post__upload--file--details{
    display: -webkit-flex;
    display: flex;
    text-align: end;
}
.PostsDisplay__Header{
    display: -webkit-flex;
    display: flex;
    background: #fff;
    -webkit-align-items: center;
            align-items: center;
    padding:0.7rem;
}

.PostsDisplay__Header--avatar{
    border-radius: 50%;
    width:2.5rem;
    height:2.5rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    color:#fff;
    background: #f83a63;
    text-transform: uppercase;
}

.PostsDisplay__Header--username{
    margin-left:0.5rem;
    text-transform: capitalize;
}

.PostsDisplay__image{
    width: 100%;
    overflow: hidden;
}
.PostsDisplay__image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}



.Comments__input{
    height: 2.5rem;
    width: 100%;
    outline: none;
    border-top:1px solid #efefef;
    border-bottom:1px solid #efefef;
    border-left:none;
    border-right:none;
    padding:0 0.5rem;
}

.Comments__main{
    display: -webkit-flex;
    display: flex;
    box-sizing: content-box;
    width: 96%;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
    padding:0 0 0.7rem 0.7rem;
}

.Comments__main--name{
    margin-right: 0.3rem;
    font-size: 14px;
    font-weight: 700;
    text-transform: capitalize;
    cursor: pointer;
    border-bottom: transparent solid 3px;
}

.Comments__main--name:hover{
    border-bottom: black solid 3px;
}

.Comments__main--comment{
    font-size: 14px;
    margin-left: 0.2rem;
    font-weight: 400;
    word-break: break-all;
}
 
@media only screen and (max-width: 936px){
.Comments__main{
    display:block;
    box-sizing: content-box;
    min-width: 0;
    /*width: 96%;*/
    text-align: left;
    margin:0 1px;
    padding-left:0;
}
.Comments__main--name{
    min-width: 0;
    margin-right: 0;
    display: table-cell;
}



}

